.header {
    background-image: linear-gradient(to bottom, rgba(32, 18, 111, 0) 10%, rgba(13, 70, 159, 0) 100%);
    box-shadow: 0 6px 30px rgba(0, 0, 0, 2);
    position: relative;
    z-index: 2;

    &--transform-none {
        transition-duration: unset ;
        transform: unset
    }
}

.header__container {

}
.nav {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -20px;
    margin-right: -20px;

    @media (max-width: 1300px) {
        flex-direction: column;
    }
}
.nav__item {
    text-decoration: white;
    color: white;
    margin-left: 20px;
    margin-right: 20px;
    transition: 0.3s;

    &:hover {
        color: white;
    }

    @media (max-width: 1300px) {
        font-size: 24px;
        margin: 15px 10px;
        font-weight: 700;
    }

    @media (max-width: 425px) {
        font-size: 18px;
    }
}

.header__flex {
    display: flex;
    justify-content: space-between;
    margin-left: -40px;
    margin-right: -40px;

    & > .header__box:last-of-type {
        border-right: unset;
    }

    @media (max-width: 1300px) {
        margin-left: unset;
        margin-right: unset;
    }
}
.nav__hamburger {
    display: none;
    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
    width: 40px;
    height: 40px;
    cursor: pointer;
    border: 2px solid transparent;

    @media (max-width: 1300px) {
        display: flex;
        position: relative;
        z-index: 3;
    }

    @media (max-width: 550px) {
        width: 34px;
        height: 30px;
    }
}

.header__box {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 20px;


    @media (max-width: 1300px) {
        border: unset;
        flex-grow: unset;
        padding: 25px 0;
    }

    @media (max-width: 767px) {
        padding: 15px 0;
    }
}

.header__logo-link {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__logo-star {
    animation: 2s linear 0s normal none infinite running rot;
}

@keyframes rot {
    0% {
        transform: rotateY(0deg);
    }

    100% {
        transform: rotateY(360deg);
    }
}

.header__logo-text {
    margin-left: 2px;
    margin-top: 3px;
}

.header__social {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -7px;
    margin-right: -7px;
}

.header__social-link {
    text-decoration: unset;
    margin-left: 7px;
    margin-right: 7px;

    path {
        transition: 0.3s;
    }

    &:hover {
        path {
            fill: yellow;
        }
    }

    svg {
        @media (max-width: 1300px) {
            width: 30px;
            height: auto;
        }
    }

    @media (max-width: 1300px) {
        margin: 15px;
    }
}

.nav__bar {
    display: block;
    width: 24px;
    height: 2px;
    background-color: #fff;
    margin: unset;
    transition: 0.15s;

    &:not(:last-child) {
        margin: 0 0 7px;
    }
}
.header__lang-switcher {
    display: flex;
    align-items: center;
    justify-content: center;
}
a:-webkit-any-link {
    cursor: pointer;
    text-decoration: underline;
}
.header__lang {
    padding: 9px;
    background-color: transparent;
    border-radius: 3px;
    text-decoration: unset;
    color: inherit;
    transition: 0.3s;
    border: 1px solid transparent;

    &:not(.header__lang--active):hover {
        border: 1px solid white;
    }

    &--active {
        background-color: #00ff7b;
        pointer-events: none;
    }
}

.header__sign-in {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: unset;
    color: inherit;

    span, svg path {
        transition: .3s;
    }

    span {
        margin-left: 10px;
        color: inherit;
    }

    &:hover {
        span {
            color: #ffffff;
        }

        svg {
            path {
                fill: #ffffff;
            }
        }
    }

    @media (max-width: 1300px) {
        display: none;
    }
}

.header__mob-menu {
    display: flex;
    flex-grow: 1;

    @media (max-width: 1300px) {
        display: none;
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        padding-top: 60px;
        z-index: 2;
        overflow-y: scroll;
        background-image: linear-gradient(to bottom, rgba(32, 18, 111, 0.29) 10%, rgba(13, 70, 159, 0.28) 100%);
        flex-direction: column;
        align-items: center;
        height: 100vh;
    }

    &.open {
        display: flex;
    }

    @media (max-width: 425px) {
        padding-top: 35px;
    }
}

.header-mob__btn {
    display: none !important;
    min-width: 180px;
    margin-top: 35px;
    position: relative;
    align-items: center;
    justify-content: center;
    padding: 15px 35px;
    border: 1px solid #fff;
    background-color: transparent;
    border-radius: 35px;
    color: #fff;
    text-decoration: unset;
    transition: .3s;
    font-weight: 700;
    outline: transparent;
    cursor: pointer;

    @media (max-width: 1300px) {
        display: inline-flex !important;
    }
}
