.planets-block {
  padding: 30px 0;
}

.planets-list {
  display: grid;
  grid-template-columns: repeat(8, auto);
}

.planet {
  color: #fff;
  background: rgba(0, 0, 0, 0);
  cursor: pointer;
}

.planets-list {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none !important;
  margin: 0 !important;
  padding: 0 !important;
}

.planet-btn-item {
  width: 12%;
  height: 45px;
  margin: 0 5px;
}

.planet-btn {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #7209b7;
  color: #7209b7;
  text-transform: uppercase;
  font-weight: 500  !important;
  font-size: 14px !important;
  overflow: hidden;
  border-radius: 4px;
  line-height: 1;
  transition: all .4s ease-in-out;
}

.planet-btn::before {
  content: '';
