@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";

.Table {
  position: relative;

  .userName {
    margin: 0 20px 0 0;
    padding: 10px;
    background-color: rgba(101, 231, 255, 0.82);
    font-size: 1.25rem;
    border-radius: 5px;

    @include media-breakpoint-up(xl) {
      display: none;
    }
  }

  .header {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;
    margin: 40px 0 20px;

    .close {
      display: flex;
      align-items: center;
      position: relative;
      cursor: pointer;
      width: 40px;
      height: 40px;
      right: 0;

      .closeIT, .closeIB {
        width: 100%;
        height: 7px;
        display: inline-block;
        position: absolute;

        border-radius: 15px;
        background-color: #65e7ff;
        transition: all .4s ease-in-out;
      }

      .closeIT {
        transform: rotate(-45deg);
      }

      .closeIB {
        transform: rotate(45deg);
      }

      &:hover .closeIT {
        transform: rotate(45deg);
      }
      &:hover .closeIB {
        transform: rotate(-45deg);
      }

      @include media-breakpoint-up(xl) {
        width: 47px;
      }
    }

    .title {
      text-transform: uppercase;
      text-align: center;
      font-weight: 800;
      font-size: 26px;
      line-height: 1;
      color: rgba(101, 231, 255, 0.82);
      margin: 0;

      @include media-breakpoint-up(xl) {
        justify-content: flex-start;
        text-align: left;
        font-size: 62px;
      }
    }
  }

  .nav {
    display: flex;
    flex-wrap: nowrap;
    margin-bottom: 30px;

    a {
      display: inline-flex;
      padding: 13px 10px;
      line-height: 28px;
      font-weight: bold;
      font-size: 24px;
      color: #ffbe6982;

      + a {
        margin-left: 15px;
      }

      &.active {
        color: #ffbf69;
      }
    }

    @include media-breakpoint-up(sm) {
      a {
        line-height: 34px;
        font-size: 28px;
      }
    }
  }

  .container {
    padding-bottom: 40px;

    @include media-breakpoint-up(xl) {
      display: flex;
    }
  }

  .sidebar {
    .matrixSelect {
      width: 100%;
      position: relative;
      margin-bottom: 25px;
      z-index: 3;
    }

    @include media-breakpoint-up(xl) {
      flex: 0 0 320px;

      .footer {
        display: block;
        margin-top: 100px;
      }
    }
  }

  .content {
    .footer {
      display: block;
    }

    button {
      min-height: 44px;
    }

    @include media-breakpoint-up(xl) {
      padding-left: 40px;
      flex: 1;

      .footer {
        display: none;
      }
    }
  }

  .footer {
    display: none;
    max-width: 320px;
    margin: 0 auto;

    .price {
      text-transform: uppercase;
      text-align: center;
      line-height: 34px;
      font-weight: 700;
      font-size: 32px;
      margin: 25px 0;
      color: #cbf3f0;

      @include media-breakpoint-up(xl) {
        line-height: 34px;
        font-size: 32px;
      }
    }

    button {
      margin-bottom: 55px;

      @include media-breakpoint-up(xl) {
        margin-bottom: 25px;
      }
    }
  }

  .navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px auto 0;
    max-width: 258px;
    height: 87px;

    button {
      transition: transform 0.3s ease-in;
      background-color: transparent;
      outline: none;
      border: none;
      padding: 0;
      margin: 0;

      &:first-child:hover {
        transform: translateX(-10px);
      }

      &:last-child:hover {
        transform: translateX(10px);
      }

      img {
        max-width: 80px;
      }
    }
  }

  .matrixTree {
    max-width: 327px;
    margin: 30px auto;
    padding: 0;

    @include media-breakpoint-up(xl) {
      max-width: 706px;
    }


    .secondRow,
    .thirdRow {
      display: flex;
      justify-content: space-between;
    }

    .secondRow {
      max-width: 242px;
      margin: 0 auto;

      @include media-breakpoint-up(xl) {
        width: 100%;
        max-width: 498px;
      }
    }

    .thirdRow {
      @include media-breakpoint-up(xl) {
        width: 100%;
        max-width: 706px;
      }


      .cellsWrapper {
        display: flex;

        & > *:first-child {
          margin-right: 27px;

          @include media-breakpoint-up(xl) {
            margin-right: 116px;
          }
        }
      }
    }
  }
}
